import React, { Component } from "react"
import Layout from "../components/layout"
import { Link } from "gatsby"
import SEO from "../components/seo"
const SimplexNoise = require('simplex-noise')

const { PI, cos, sin, abs, sqrt, pow, floor, round, random, atan2 } = Math;
const HALF_PI = 0.5 * PI;
const TAU = 2 * PI;
const TO_RAD = PI / 180;
const rand = n => n * random();
const randIn = (min, max) => rand(max - min) + min;
const randRange = n => n - rand(2 * n);
const fadeIn = (t, m) => t / m;
const fadeOut = (t, m) => (m - t) / m;
const fadeInOut = (t, m) => {
  let hm = 0.5 * m;
  return abs((t + hm) % m - hm) / (hm);
};
const dist = (x1, y1, x2, y2) => sqrt(pow(x2 - x1, 2) + pow(y2 - y1, 2));
const angle = (x1, y1, x2, y2) => atan2(y2 - y1, x2 - x1);
const lerp = (n1, n2, speed) => (1 - speed) * n1 + speed * n2;

const particleCount = 1000;
const spawnRadius = 100;
const noiseSteps = 6;

let center;
let tick;
let simplex;
let positions;
let velocities;
let speeds;
let lifeSpans;
let sizes;
let hues;
let c, $;
let c1, $1;

export default class IndexPage extends Component {

  constructor(props) {
    super(props);
    this.createParticles = this.createParticles.bind(this);
    this.initParticle = this.initParticle.bind(this);
    this.draw = this.draw.bind(this);
    this.drawParticle = this.drawParticle.bind(this);
    this.checkBounds = this.checkBounds.bind(this);
    this.resize = this.resize.bind(this);
  }

  componentDidMount() {
    center = [];
    tick = 0;
    c = this.refs.canvas;
    c1 = this.refs.canvas1;
    $ = c.getContext("2d");
    $1 = c1.getContext("2d");
    this.resize();
    this.createParticles();
    this.draw();
  }

  createParticles() {
    simplex = new SimplexNoise();
    positions = new Float32Array(particleCount * 2);
    velocities = new Float32Array(particleCount * 2);
    lifeSpans = new Float32Array(particleCount * 2);
    speeds = new Float32Array(particleCount);
    hues = new Float32Array(particleCount);
    sizes = new Float32Array(particleCount);

    let i;

    for (i = 0; i < particleCount * 2; i += 2) {
      this.initParticle(i);
    }
  }

  initParticle(i) {
    let iy, ih, rd, rt, cx, sy, x, y, s, rv, vx, vy, t, h, si, l, ttl;

    iy = i + 1;
    ih = 0.5 * i | 0;
    rd = rand(spawnRadius);
    rt = rand(TAU);
    cx = cos(rt);
    sy = sin(rt);
    x = center[0] + cx * rd;
    y = center[1] + sy * rd;
    rv = randIn(0.1, 1);
    s = randIn(1, 8);
    vx = rv * cx * 0.1;
    vy = rv * sy * 0.1;
    si = randIn(0.1, 1);
    h = randIn(160, 260);
    l = 0;
    ttl = randIn(50, 200);
    positions[i] = x;
    positions[iy] = y;
    velocities[i] = vx;
    velocities[iy] = vy;
    speeds[ih] = s;
    hues[ih] = h;
    sizes[ih] = si;
    lifeSpans[i] = l;
    lifeSpans[iy] = ttl;
  }

  drawParticle(i) {
    let iy, ih, x, y, n, tx, ty, s, vx, vy, h, si, l, dl, ttl, c;

    iy = i + 1;
    ih = 0.5 * i | 0;
    x = positions[i];
    y = positions[iy];
    n = simplex.noise3D(x * 0.0025, y * 0.0025, tick * 0.0005) * TAU * noiseSteps;
    vx = lerp(velocities[i], cos(n), 0.05);
    vy = lerp(velocities[iy], sin(n), 0.05);
    s = speeds[ih];
    tx = x + vx * s;
    ty = y + vy * s;
    h = hues[ih];
    si = sizes[ih];
    l = lifeSpans[i];
    ttl = lifeSpans[iy];
    dl = fadeInOut(l, ttl);
    c = `hsla(${h},50%,60%,${dl})`;

    l++;
    $1.save();
    $1.lineWidth = dl * si + 1;
    $1.strokeStyle = c;
    $1.beginPath();
    $1.moveTo(x, y);
    $1.lineTo(tx, ty);
    $1.stroke();
    $1.closePath();
    $1.restore();

    positions[i] = tx;
    positions[iy] = ty;
    velocities[i] = vx;
    velocities[iy] = vy;
    lifeSpans[i] = l;

    (this.checkBounds(x, y) || l > ttl) && this.initParticle(i);
  }


  checkBounds(x, y) {
    return (
      x > c1.width ||
      x < 0 ||
      y > c1.height ||
      y < 0
    );
  }

  resize() {
    const { innerWidth, innerHeight } = window;

    c1.width = innerWidth;
    c1.height = innerHeight;

    $1.drawImage(c, 0, 0);

    c.width = innerWidth;
    c.height = innerHeight;

    $.drawImage(c1, 0, 0);

    center[0] = 0.5 * c1.width;
    center[1] = 0.5 * c1.height;
  }

  draw() {
    tick++;
    $1.clearRect(0, 0, c1.width, c1.height);

    $.fillStyle = 'rgba(0,0,0,0.1)';
    $.fillRect(0, 0, c.width, c.height);

    let i;

    for (i = 0; i < particleCount * 2; i += 2) {
      this.drawParticle(i);
    }

    $.save();
    $.filter = 'blur(8px)';
    $.globalCompositeOperation = 'lighten';
    $.drawImage(c1, 0, 0);
    $.restore();

    $.save();
    $.globalCompositeOperation = 'lighter';
    $.drawImage(c1, 0, 0);
    $.restore();

    window.requestAnimationFrame(this.draw);
  }

  render() {
    return (
      <>
        <canvas className="canvas" ref="canvas" />
        <canvas className="canvas" ref="canvas1" />
        <SEO title="Home" keywords={[`engine room`, `developers`, `designers`, `community`]} />
        <Layout>
          <h1 className="title">Engine Room [SG]</h1>
        </Layout >
        <div style={{ position: "absolute", left: "0", textAlign: "center", bottom: "50px", width: "100%" }}>
          <Link
            to="/page2"
            style={{
              margin: '10px',
              color: `white`,
              textDecoration: `none`,
            }}
          >
            About
          </Link>
          <Link
            to="/page2"
            style={{
              margin: '10px',
              color: `white`,
              textDecoration: `none`,
            }}
          >
            Subscribe
          </Link>
        </div>
      </>
    )
  }
};